.header {
    background-color: #f8f9fa;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar {
    display: flex;
}

.nav-list {
    list-style-type: none;
    display: flex;
    gap: 1rem;
}

.nav-item a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
}

.nav-item a:hover {
    color: #007bff;
}

.header-search {
    flex-grow: 1;
    text-align: right;
}
